/*
  1. Use a more-intuitive box-sizing model.
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

/*
    2. Remove default margin
  */
* {
  margin: 0;
}

/*
    Typographic tweaks!
    3. Add accessible line-height
    4. Improve text rendering
  */
body {
  display: grid;
  place-content: center;
  min-height: 100vh;
  background-color: rgb(223, 225, 226);
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

/*
    5. Improve media defaults
  */
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

img {
  display: block;
}

/*
    6. Remove built-in form typography styles
  */
input,
button,
textarea,
select {
  font: inherit;
}

/*
    7. Avoid text overflows
  */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

:root {
  --Purple-100: hsl(254, 88%, 90%);
  --Purple-500: hsl(256, 67%, 59%);
  --Yellow-100: hsl(31, 66%, 93%);
  --Yellow-500: hsl(39, 100%, 71%);
  --White:hsl(0, 0%, 100%);
  --Black:hsl(0, 0%, 7%);
}

:root {
  --font-regular:"DM Sans";
}

.container {
  font-family: var(--font-regular), sans-serif;
  border-radius: 0.625rem;
}
.container p {
  line-height: 0.9;
}
.container h2 {
  font-weight: 500;
  line-height: 0.9;
  letter-spacing: -0.0625rem;
}

main {
  display: grid;
  gap: 1.25rem;
  margin: 0 auto;
  width: min(95%, 75rem);
  grid-template-areas: "area1" "area2" "area3" "area4" "area5" "area6" "area7" "area8";
}
@media (min-width: 71.875em) {
  main {
    grid-template-columns: 0.7fr 0.6fr 0.6fr 0.6fr;
    grid-template-rows: 0.5fr 0.2fr 0.3fr 0.6fr;
    grid-template-areas: "area7 area1 area1 area4" "area7 area2 area3 area4" "area8 area2 area3 area4" "area8 area6 area5 area5";
    align-items: stretch;
  }
}

.grid-container1 {
  text-align: center;
  background-color: var(--Purple-500);
  padding: 2.0625rem;
  grid-area: area1;
}
.grid-container1__h {
  font-size: 2.5rem;
  color: var(--White);
  padding: 0 2.5rem;
}
.grid-container1__h :nth-child(1) {
  color: var(--Yellow-500);
}
.grid-container1__img {
  width: 11.875rem;
  margin: 0.625rem auto;
  margin-top: 0.625rem;
}
.grid-container1__p {
  color: var(--White);
}

.grid-container2 {
  grid-area: area2;
  background-color: var(--White);
  text-align: left;
  padding: 1rem 0.9375rem;
}
.grid-container2 img {
  width: 18.75rem;
}
@media (min-width: 71.875em) {
  .grid-container2 {
    overflow: hidden;
  }
  .grid-container2__h {
    font-size: 2.1875rem;
  }
}

.grid-container3 {
  grid-area: area3;
  padding: 0.9375rem 0.9375rem 0 0.9375rem;
  background-color: var(--Yellow-500);
}
.grid-container3__h {
  text-align: left;
}
.grid-container3 img {
  width: 12.5rem;
  margin-top: 0.9375rem;
}
@media (min-width: 71.875em) {
  .grid-container3 {
    overflow: hidden;
  }
  .grid-container3__h {
    font-size: 2.1875rem;
  }
}

.grid-container4 {
  grid-area: area4;
  padding: 1.625rem 1.125rem;
  text-align: center;
  background-color: var(--Purple-100);
}
.grid-container4__h {
  font-size: 1.5625rem;
}
.grid-container4 img {
  width: 20rem;
  margin: 1.25rem auto;
}
.grid-container4__p {
  font-size: 1.1875rem;
  padding: 0 0.6875rem;
}
@media (min-width: 71.875em) {
  .grid-container4 {
    overflow: hidden;
    text-align: left;
  }
  .grid-container4__p {
    padding: 0;
  }
  .grid-container4 img {
    position: relative;
    left: 1.25rem;
  }
}

.grid-container5 {
  display: flex;
  flex-direction: column;
  grid-area: area5;
  background-color: var(--Purple-500);
  padding: 1.125rem 0;
}
.grid-container5 h2 {
  text-align: center;
  color: var(--White);
  padding: 0 3.125rem;
  font-size: 2.0625rem;
}
.grid-container5 img {
  margin: 0 auto;
  margin-bottom: 1.875rem;
  width: 15.625rem;
}
@media (min-width: 71.875em) {
  .grid-container5 {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 20px;
    padding-right: 0;
  }
  .grid-container5 img {
    padding: 0;
    margin: 0;
  }
  .grid-container5 h2 {
    font-size: 1.875rem;
    text-align: left;
  }
}

.grid-container6 {
  grid-area: area6;
  text-align: left;
  padding: 1.875rem;
  background-color: var(--White);
}
.grid-container6__h {
  font-size: 3rem;
}
.grid-container6 img {
  width: 12.5rem;
}
.grid-container6 p {
  margin: 1.25rem 0;
  padding: 0;
}

.grid-container7 {
  grid-area: area7;
  background-color: var(--Yellow-100);
  text-align: left;
  padding: 1.875rem;
}
.grid-container7__h {
  font-size: 1.875rem;
}
.grid-container7 img {
  margin-top: 1.25rem;
  width: 12.5rem;
}
.grid-container7 i {
  color: var(--Purple-500);
}

.grid-container8 {
  grid-area: area8;
  background-color: var(--Yellow-500);
  padding: 1.875rem;
}
.grid-container8__h {
  font-size: 1.875rem;
}
.grid-container8 img {
  width: 13.75rem;
  margin-top: 1.875rem;
}